import React, { Component } from 'react';
import { Text, TextInput, View, TouchableOpacity } from 'react-native';

export default class Bevitel extends Component {
  constructor(props) {
    super(props);
    this.state = {
        nev: '',
        komment: '',
    };
  }

  felvitel=async () =>{
   // alert("Megnyomva!")
    let bemenet ={
      bevitel1:this.state.nev,
      bevitel2:this.state.komment,
      bevitel3:this.props.akttema_bevitel
    }
      fetch('https://s1.siralycore.hu:8083/uzenetfelvitel', {
      method: "POST",
      body: JSON.stringify(bemenet),
      headers: {"Content-type": "application/json; charset=UTF-8"}
      } )
      .then((response) => response.text())
      .then((szoveg) => {

      //alert(szoveg)
      this.setState({nev:""})
      this.setState({komment:""})
      this.props.frissit()
      
        
      })
      .catch((error) =>{
        console.error(error);
      });

  }


  render() {
    return (

      <View style={{padding: 10, backgroundColor:"lightblue", alignItems:"center",margin:12, borderRadius:10}}>
          <Text style={{padding: 10, fontSize: 20, color:"black",}}>
          NÃ©v
        </Text>

        <TextInput
          placeholderTextColor="black"
          style={{height: 45,backgroundColor:"#DCDCDC", borderRadius:10, padding:10}}
          placeholder="Add meg a nevedet!"
          onChangeText={(nev) => this.setState({nev})}
          value={this.state.nev}
        />

<Text style={{padding: 10, fontSize: 20, color:"black",height:80,textAlignVertical:"top", }}>
          Komment:
        </Text>

        <TextInput
          placeholderTextColor="black"
          style={{height: 150, width:300, textAlignVertical:"top", backgroundColor:"#DCDCDC", borderRadius:10, padding:10,}}
          placeholder="Add meg a kommented!"
          onChangeText={(komment) => this.setState({komment})}
          value={this.state.komment}
          multiline={true}
        />
        <TouchableOpacity 
        onPress={async ()=> this.felvitel()}>
          <View style={{width:120,backgroundColor:"green", borderRadius:10,padding:5, marginTop:15,}}>
            <Text style={{textAlign:"center", fontSize:20,}}>Felvitel</Text>
          </View>
        </TouchableOpacity>
        
      </View>
     
    );
  }
}